<template>
  <div class="container">
    <div class="form">
      <el-form :inline="true" :model="houseForm" class="demo-form-inline" size="small" style="padding-right: 0">
        <el-row>
          <el-col :span="7">
            <el-form-item label="">
              <el-select v-model="houseForm.styleName" placeholder=""><el-option :label="item.styleName" :value="item.styleName" v-for="(item, index) in styleList" :key="index"></el-option></el-select> </el-form-item
          ></el-col>
          <el-col :span="7"
            ><el-form-item label="">
              <el-select v-model="houseForm.floor" placeholder=""> <el-option :label="item.floorName" :value="item.floorName" v-for="(item, index) in floorList" :key="index"></el-option></el-select> </el-form-item
          ></el-col>
          <el-col :span="7"
            ><el-form-item label="">
              <el-select v-model="houseForm.balcony" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option
              ></el-select> </el-form-item
          ></el-col>
          <el-col :span="3">
            <el-form-item>
              <el-button type="primary" @click="getHouse"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="table">
      <p style="color: #303133; font-size: 14px"></p>
      <el-table :data="tableData" style="width: 100%" @row-click="singleElection" highlight-current-row height="450" size="mini" border>
        <el-table-column width="55" label="">
          <template slot-scope="scope">
            <!-- label -->
            <el-radio class="radio" v-model="templateSelection" :label="scope.row.houseId">&nbsp;</el-radio>
          </template>
        </el-table-column>
        <el-table-column prop="houseName" label="" width="80"> </el-table-column>
        <el-table-column prop="styleName" label=""> </el-table-column>
        <el-table-column prop="balcony" label="" width="80"> </el-table-column>
        <el-table-column prop="area" label="()" width="80"> </el-table-column>
        <el-table-column prop="price" label="(/)" width="120"></el-table-column>
        <template #empty>
          <el-empty :image-size="80"></el-empty>
        </template>
      </el-table>
    </div>
    <div class="btn">
      <el-button type="primary" @click="submit"> </el-button>
      <el-button @click="close"> </el-button>
    </div>
  </div>
</template>

<script>
import { getFloor } from '@/api/floor';
import { getAllStyleName } from '@/api/house_type';
import { getHouse } from '@/api/house';
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      houseForm: {
        styleName: '',
        floor: '',
        balcony: ''
      },
      tableData: [],
      //   id
      templateSelection: '',
      //   
      checkList: [],
      floorList: [],
      styleList: [],
      newUser: {}
    };
  },
  created() {
    this.getAll();
  },
  methods: {
    async getAll() {
      try {
        const { data: floor } = await getFloor();
        this.floorList = floor;
        const { data: style } = await getAllStyleName();
        this.styleList = style;
        this.getHouse();
      } catch (error) {
        console.log(error);
      }
    },
    //
    async getHouse() {
      try {
        // console.log(this.houseForm);
        const { data } = await getHouse(this.houseForm);
        this.tableData = data;
        this.resetHouse();
      } catch (error) {
        console.log(error);
      }
    },
    //
    resetHouse() {
      this.houseForm = {
        styleName: '',
        floor: '',
        balcony: ''
      };
    },
    //teble 
    singleElection(row) {
      // console.log(row);
      this.templateSelection = row.houseId;
      this.checkList = this.tableData.filter(item => item.houseId === row.houseId);
      // console.log(`${row.houseId}`);
    },
    submit() {
      if (this.checkList.length === 0) {
        this.$message.error('');
      } else {
        console.log(this.checkList);
        this.$emit(`changeUpdateHouseComponent`, {
          houseData: this.checkList,
          component: `UpdateContract`
        });
      }
    },
    //
    close() {
      this.newUser = this.user;
      this.$emit('afterUpdateHouse', this.newUser);
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  width: 90%;
  margin: 0 auto;
  .form {
    ::v-deep .el-form {
      .el-input__inner {
        width: 150px;
      }
      .el-form-item__label {
        margin-right: 5px;
      }
    }
  }
  .table {
    p {
      margin-bottom: 15px;
    }
  }
  .btn {
    margin-top: 15px;
    padding-left: 5px;
  }
}
</style>
