<template>
  <div class="container">
    <div class="bg" style="padding-top: 5px">
      <div class="main">
        <el-descriptions title=" " :column="3">
          <template slot="extra">
            <el-button type="primary" size="small" @click="updateIdcard" :disabled="isEdit || isOut"></el-button>
          </template>
          <el-descriptions-item label="">{{ user.username }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.name }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.sex }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.nation }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.birthday }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.idcard }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.phone }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.sign }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.lose }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.authority }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.address }}</el-descriptions-item>
        </el-descriptions>
      </div>
    </div>
    <div class="bg">
      <div class="main">
        <el-descriptions title=" " :column="3">
          <template slot="extra">
            <el-button type="primary" size="small" @click="updateHouse" :disabled="isEdit || isOut"></el-button>
          </template>
          <el-descriptions-item label="">{{ user.houseName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.styleName }}</el-descriptions-item>
          <el-descriptions-item label="()">{{ user.area }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.balcony }}</el-descriptions-item>
          <el-descriptions-item label="(/)">{{ user.price }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.loginRent }}</el-descriptions-item>
          <el-descriptions-item label="">{{ user.logoutRent }}</el-descriptions-item>
        </el-descriptions>
      </div>
    </div>
    <div class="bg">
      <div class="main">
        <el-descriptions title=" ">
          <template slot="extra">
            <el-button type="primary" size="small" @click="updateBasic" :disabled="isEdit || isOut"></el-button>
          </template></el-descriptions
        >
        <el-table :data="tableData" stripe style="width: 100%" height="250" size="small">
          <el-table-column prop="baseName" label=""> </el-table-column>
          <el-table-column prop="price" label="(/)" width="120"> </el-table-column>
          <el-table-column prop="remark" label="" width="280"> </el-table-column>
          <el-table-column prop="time" label=""> </el-table-column>
          <template #empty>
            <el-empty :image-size="80"></el-empty>
          </template>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {};
      }
    },
    tableData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    isEdit: {
      type: Boolean,
      default: true
    },
    isDel: {
      type: Boolean,
      default: true
    },
    isOut: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    // 
    updateIdcard() {
      this.$emit(`updateIdcard`, this.user);
    },
    //
    updateHouse() {
      this.$emit(`updateHouse`, this.user);
    },
    //
    updateBasic() {
      this.$emit(`updateBasic`, this.user);
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  .bg {
    width: 100%;
    padding: 25px 25px 0 25px;
    .main {
      padding: 10px;
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    }
  }
}
</style>
