<template>
  <div class="container">
    <div v-if="active == 0">
      <p class="header">{{ order.outTradeNo }}</p>
      <div class="pay">{{ payTitle }}</div>
      <p class="header"></p>
      <div style="margin-top: 30px; padding-left: 15px">
        <el-button @click="alipay"><i style="margin-right: 5px; color: #1296db; font-size: 20px" class="iconfont">&#xe634;</i></el-button>
        <el-button @click="cashpay"><i style="margin-right: 5px; color: #1296db; font-size: 20px" class="iconfont">&#xea5b;</i></el-button>
      </div>
      <p style="color: #303133; font-size: 14px; padding: 25px 15px 0">*<el-button type="text" @click="judgePay"></el-button></p>
    </div>
    <div v-if="active == 1">
      <el-result icon="success" title="" :subTitle="`${count}`">
        <template slot="extra">
          <el-button type="primary" size="medium" @click="close"></el-button>
        </template>
      </el-result>
    </div>
  </div>
</template>

<script>
import { payByCash, payByAlipay, judgePay } from '@/api/order';
export default {
  data() {
    return {
      active: 0,
      count: '', //
      timer: ''
    };
  },
  props: {
    order: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  computed: {
    payTitle() {
      console.log(this.order);
      return `${this.order.total}`;
    }
  },
  methods: {
    //
    async alipay() {
      this.judgePay();
      try {
        //  
        const { data } = await payByAlipay({
          ...this.order,
          subject: '',
          userId: this.$store.getters.userId
        });
        // console.log(data);
        const newWindow = window.open('', '_target');
        newWindow.document.write(data);
        newWindow.focus();
      } catch (error) {
        console.log(error);
      }
    },
    //
    cashpay() {
      this.judgePay();
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await payByCash({
          ...this.order,
          userId: this.$store.getters.userId
        });
        this.active++;
      });
    },
    //
    async judgePay() {
      try {
        const { data } = await judgePay(this.order);
        if (data > 0) {
          this.$message.success('');
          this.payTitle = `${this.order.total}`;
          this.active++;
        }
      } catch (error) {
        console.log(error);
      }
    },
    //
    countDown() {
      const TIME_COUNT = 3;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.timer = setInterval(() => {
          if (this.count > 1 && this.count <= TIME_COUNT) {
            //
            this.count--;
          } else {
            clearInterval(this.timer); //
            this.timer = null;
            this.close();
          }
        }, 1000);
      }
    },
    //
    close() {
      this.$emit(`closePayDrawer`);
    }
  },
  watch: {
    active(newVal, oldVal) {
      if (newVal == 1) {
        this.countDown();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  width: 90%;
  margin: 0 auto;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 15px;
  height: 400px;
  .pay {
    width: 100%;
    height: 150px;
    font-size: 50px;
    line-height: 150px;
    text-align: center;
  }
  .header {
    color: #303133;
    font-size: 18px;
    border-left: 5px solid #3a3f63;
    padding-left: 5px;
  }
}
</style>
