<template>
  <div class="container">
    <div class="table">
      <p style="color: #303133; font-size: 14px"></p>
      <el-table :data="tableData" stripe style="width: 100%" height="250" size="small" border>
        <el-table-column prop="baseName" label="" width="100"> </el-table-column>
        <el-table-column prop="price" label="(/)" width="100"> </el-table-column>
        <el-table-column prop="remark" label=""> </el-table-column>
        <el-table-column prop="time" label=""> </el-table-column>
        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button @click="deleteRow(scope.row)" type="text" size="mini" :disabled="isDel">  </el-button>
          </template>
        </el-table-column>
        <template #empty>
          <el-empty :image-size="80"></el-empty>
        </template>
      </el-table>
    </div>
    <div class="table">
      <p style="color: #303133; font-size: 14px">:</p>
      <el-table :data="noBasicData" stripe style="width: 100%" height="250" size="small" border>
        <el-table-column prop="baseName" label="" width="120"> </el-table-column>
        <el-table-column prop="price" label="(/)" width="120"> </el-table-column>
        <el-table-column prop="remark" label=""> </el-table-column>
        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button @click="addRow(scope.row)" type="text" size="mini" :disabled="isAdd">  </el-button>
          </template>
        </el-table-column>
        <template #empty>
          <el-empty :image-size="80"></el-empty>
        </template>
      </el-table>
    </div>
    <div class="btn">
      <el-button @click="close" type="primary"></el-button>
    </div>
  </div>
</template>

<script>
import { changeMiscellaneousState, insertMiscellaneous, getNoBasicByRentId } from '@/api/basic';
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {};
      }
    },
    tableData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    isDel: {
      type: Boolean,
      default: true
    },
    isAdd: {
      type: Boolean,
      default: true
    }
  },
  created() {
    this.getNoBasic();
  },
  data() {
    return {
      newTableData: [],
      noBasicData: []
    };
  },
  methods: {
    //
    async getNoBasic() {
      try {
        const { data } = await getNoBasicByRentId({ rentId: this.user.rentId });
        console.log(data);
        this.noBasicData = data;
      } catch (error) {
        console.log(error);
      }
    },
    //
    async deleteRow(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          try {
            await changeMiscellaneousState({
              state: 2,
              miscellaneousId: row.miscellaneousId
            });
            this.$message.success('');
          } catch (error) {
            console.log(error);
          } finally {
            this.$emit(`deleteMiscellaneous`);
            this.getNoBasic();
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    //
    async addRow(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          try {
            await insertMiscellaneous({
              rentId: this.user.rentId,
              baseId: row.baseId
            });
            this.$message.success('');
          } catch (error) {
            console.log(error);
          } finally {
            this.$emit(`deleteMiscellaneous`);
            this.getNoBasic();
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    //
    close() {
      this.newTableData = this.tableData;
      this.$emit('afterUpdateBasic', this.newTableData);
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  width: 90%;
  margin: 0 auto;
  .table {
    margin-bottom: 15px;
  }
}
</style>
